.ed-card{
   width: 300px;
   height: 300px;
   cursor: pointer;
   background-color: black;
   transform: scale(0.75) rotateY(0deg) rotateX(45deg) translateZ(4.5rem);
   transform-origin: 50% 100%;
   transform-style: preserve-3d;
   box-shadow: 1rem 1rem 2rem rgba(0, 224, 198,0.5);
   transition: 0.6s ease transform;
}

 .ed-card:hover{
   transform: scale(1);
  
 }

 .ed-card::before{
   transform: translateZ(4rem);
   
 }

 .ed-card::before:hover{
   transform: translateZ(0);
 }

 .ed-card::after{
   transform: translateZ(-4rem);
 }

 .ed-card::after:hover{
     transform: translateZ(-1px);
 }

 .ed-card:hover > .pitt-hover{
   display: flex;
   align-items: center;
   text-align: center;
   position:absolute;
   top:40%;
}

.ed-card:hover > .google-hover{
   display: flex;
   align-items: center;
